.switch {
  width: 64px;
  height: 32px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.switch__input, .switch__label {
  position: absolute;
  top: 0;
  left: 0;
}

.switch__input {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
}

.switch__input:checked + .switch__label {
  background-color: #fd5103;
}

.switch__input:checked + .switch__label + .switch__marker {
  left: calc(100% - 29px);
}

.switch__label {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  background-color: #5f6775;
  transition: background 1s;
}

.switch__marker {
  width: 26px;
  height: 26px;
  z-index: 2;
  pointer-events: none;
  will-change: left;
  background-color: #fff;
  border-radius: 50%;
  transition: left 1s;
  position: relative;
  left: 3px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

